html {
  text-align: center;
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;
  background: #566991;
}

body {
  padding: 1em;
}

* {
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 5em;
}

a {
  font-size: 1.5em;
  text-decoration: none;
  font-weight: bold;
}

p {
  margin: 1em;
}

button {
  padding: 0.5em;
  font-size: 1em;
  border-radius: 0.5em;
  color: #fff;
  border: none;
  cursor: pointer;
}

button:disabled {
  background-color: #fa7a3e;
}

.container {
  width: 70%;
  margin: 0 auto;
  border-radius: 1em;
  background-color: #687fad;
}

.home-containerm, .question-container {
  padding: 1em;
}

.home-container button {
  background-color: #2d60c4;
}

.answer-btn, .answer-btn-correct {
  background-color: #e6ab67;
  padding: 1em;
  margin: 0.5em;
  font-size: 1.5em;
}

.answer-btn-correct:disabled {
  background-color: #77a880;
}

.navigation-container {
  margin-top: 1em;
}

.navigation-container button {
  color: #485773;
  font-size: 1.3em;
}

.start-btn {
  color: #fff;
}

.quit-btn {
  float: left;
}

.next-btn {
  float: right;
}

.score {
  font-size: 2em;
}

.question {
  font-size: 1.5em;
}

@media screen and (max-width: 450px) {
  html {
    font-size: 12px;
  }

  .container {
    width: 90%;
  }

  .answer-btn {
    display: block;
    margin: 0.5em auto;
  }
}